import { fetchData } from '@/utils/fetch';
// http://wiki.tools.msparis.com/pages/viewpage.action?pageId=1015930
export const getBanner = (params) =>
  fetchData('api/web/banner/query', {
    method: 'POST',
    body: JSON.stringify(params),
    config: {
      server: false, // 
      cache: 'no-store', // 
      errorMessage: '',
      localStorage: {
        keys: ['i18nextLng'], // 
        merge: {
          enabled: true,
          strategy: 'preserve' // 
        }
      }
    }
  });
