/**
 *  Next.js fetch 
 * @param {string} url - 
 * @param {Object} options - fetch
 * @param {Object} options.config - 
 * @returns {Promise} 
 */
import { notFound } from 'next/navigation';

export async function fetchData(url, { config = {}, ...options } = {}) {
  // 
  const defaultConfig = {
    baseURL: process.env.NEXT_PUBLIC_API_BASE_URL || '',
    cache: 'force-cache',
    revalidate: 60,
    server: true,
    errorMessage: '',
    withCredentials: false,
    // 
    debug: {
      enabled: process.env.NODE_ENV === 'development', // 
      logRequest: true, // 
      logResponse: true, // 
      logCache: false, // 
      logLocalStorage: false // localStorage
    },
    // 
    localCache: {
      enabled: false,
      ttl: 60,
      key: null
    },
    // localStorage
    localStorage: {
      keys: [],
      merge: {
        enabled: false,
        strategy: 'preserve'
      }
    }
  };

  // 
  const defaultHeaders = {
    'Accept': 'application/json',
    'X-Requested-With': 'XMLHttpRequest',
    'Access-Control-Allow-Credentials': true,
    'Access-Control-Allow-Origin': process.env.NEXT_PUBLIC_API_BASE_URL,
    'Access-Control-Allow-Methods': 'DELETE, HEAD, GET, OPTIONS, POST, PUT',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Headers': 'Content-Type, Content-Range, Content-Disposition, Content-Description',
    'Access-Control-Max-Age': 1728000
  };

  // 
  const serverDefaultHeaders = {
    'X-API-Key': process.env.API_KEY || '',
    'X-Server-Request': 'true'
  };

  // 
  const { 
    baseURL,
    cache, 
    revalidate, 
    server, 
    errorMessage,
    withCredentials,
    debug,
    localCache,
    localStorage: { keys: localStorageKeys, merge: mergeConfig }
  } = { ...defaultConfig, ...config };

  // 
  const cacheKey = localCache.key || `${url}-${options.method || 'GET'}`;

  // 
  const debugLog = (message, data = null) => {
    if (debug.enabled) {
      const prefix = `[fetchData] [${new Date().toISOString()}]`;
      if (data) {
        console.log(`${prefix} ${message}`, data);
      } else {
        console.log(`${prefix} ${message}`);
      }
    }
  };

  try {
    // 1. 
    debugLog(`: ${url}`, {
      method: options.method || 'GET',
      server,
      cacheStrategy: cache,
      revalidate
    });

    // 2. localStorage
    const localStorageData = {};
    if (!server && mergeConfig.enabled && localStorageKeys.length > 0) {
      try {
        localStorageKeys.forEach(key => {
          const storedValue = localStorage.getItem(key);
          if (storedValue !== null) {
            try {
              localStorageData[key] = JSON.parse(storedValue);
            } catch {
              localStorageData[key] = storedValue;
            }
          }
        });
        if (debug.logLocalStorage) {
          debugLog('localStorage', localStorageData);
        }
      } catch (err) {
        debugLog('localStorage', err);
      }
    }

    // 3. 
    let cacheHit = false;
    if (!server && localCache.enabled) {
      try {
        const cached = localStorage.getItem(cacheKey);
        if (cached) {
          const { data, timestamp } = JSON.parse(cached);
          const age = Math.floor((Date.now() - timestamp) / 1000);
          
          if (Date.now() - timestamp < localCache.ttl * 1000) {
            cacheHit = true;
            if (debug.logCache) {
              debugLog(`: ${cacheKey} (: ${age})`, {
                ttl: localCache.ttl,
                timestamp: new Date(timestamp).toISOString()
              });
            }
            return { data, localStorageData, debug: { cacheHit, cacheKey } };
          } else {
            localStorage.removeItem(cacheKey);
            if (debug.logCache) {
              debugLog(`: ${cacheKey} ()`, { age });
            }
          }
        }
      } catch (err) {
        debugLog('', err);
      }
    }

    // 4. URL
    const fullUrl = url.startsWith('http') ? url : `${baseURL}${url}`;
    debugLog(`URL: ${fullUrl}`);

    // 5. localStorage
    let requestBody = options.body;
    const method = (options.method || 'GET').toUpperCase();

    if (!server && mergeConfig.enabled && 
        Object.keys(localStorageData).length > 0 && 
        ['POST', 'PUT', 'PATCH', 'DELETE'].includes(method)) {
      
      try {
        const originalData = requestBody 
          ? typeof requestBody === 'string' 
            ? JSON.parse(requestBody)
            : { ...requestBody }
          : {};

        let mergedData;
        if (mergeConfig.strategy === 'override') {
          mergedData = { ...localStorageData, ...originalData };
        } else {
          mergedData = { ...originalData, ...localStorageData };
        }

        requestBody = JSON.stringify(mergedData);
        if (debug.logRequest) {
          debugLog('localStorage', {
            strategy: mergeConfig.strategy,
            originalData,
            mergedData
          });
        }
      } catch (err) {
        debugLog('localStorage', err);
      }
    }

    // 6. 
    const headers = {
      ...defaultHeaders,
      ...(server && serverDefaultHeaders),
      ...options.headers
    };

    // 7. fetch
    const fetchOptions = {
      ...options,
      headers,
      credentials: withCredentials ? 'include' : 'same-origin',
      ...(requestBody && { body: requestBody }),
      next: {
        ...(options.next || {}),
        cache,
        ...(revalidate > 0 && { revalidate })
      },
      // Next.js 15+ 
      ...(debug.enabled && { debug: true })
    };

    // 8. 
    if (debug.logRequest) {
      debugLog('', {
        url: fullUrl,
        method,
        headers: { ...headers, 'Authorization': headers.Authorization ? '******' : undefined }, // 
        cache: fetchOptions.next?.cache,
        revalidate: fetchOptions.next?.revalidate,
        body: method !== 'GET' ? (requestBody ? JSON.parse(requestBody) : undefined) : undefined
      });
    }

    // 9. 
    const response = await fetch(fullUrl, fetchOptions);
    // 10. 
    if (debug.logResponse && debug.enabled) {
      debugLog('', {
        status: response.status,
        statusText: response.statusText,
        url: response.url,
        cacheStatus: response.headers.get('x-nextjs-cache') || 'unknown',
      });
    }

    if (!response.ok) {
      if (debug.enabled){
        debugLog(` [${response.status}]`, {
          url: fullUrl,
          statusText: response.statusText
        });
      }

      // 404
      if (response.status === 404) {
        // App RouternotFound()404
        // Pages Routerrouter.push('/404')
        notFound();
      }

      throw new Error(`${errorMessage} (${response.status})`);
    }

    // 11. 
    const contentType = response.headers.get('content-type');
    const data = contentType?.includes('application/json') 
      ? await response.json() 
      : await response.text();

    // 12. 
    if (debug.logResponse && debug.enabled) {
      const logData = typeof data === 'object' 
        ? Array.isArray(data) 
          ? ` (: ${data.length})` 
          : ` (: ${Object.keys(data).length})`
        : ` (: ${String(data).length})`;
      
      debugLog(`: ${logData}`, data);
    }

    // 13. 
    if (!server && localCache.enabled) {
      try {
        localStorage.setItem(cacheKey, JSON.stringify({
          data,
          timestamp: Date.now()
        }));
        if (debug.logCache) {
          debugLog(`localStorage: ${cacheKey}`);
        }
      } catch (err) {
        debugLog('', err);
      }
    }

    return data;

  } catch (error) {
    if (debug.enabled) {
     debugLog(`: ${error.message}`, error);
    }
   
    if (config.throwError !== false) {
      throw error;
    }
    return {
      status: 201,
      data: null,
    };
  }
}
